*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}
@media screen and (min-width: 320px) {
  html {
    font-size: 85.333px; 
  } 
}
@media screen and (min-width: 360px) {
  html {
    font-size: 96px; 
  }
}
@media screen and (min-width: 375px) {
  html {
      font-size: 100px;
  }
}
@media screen and (min-width: 414px) {
  html {
      font-size: 110.4px;
  }
}
body{
  font-size: 0.12rem;
}
h1{
  font-size: 0.12rem;
  background: red;
}